import { Injectable, Injector, Inject } from '@angular/core';
import { ReducerManager as NgrxReducerManager, Store as NgrxStore } from '@ngrx/store';
import { Actions as NgrxActions, EffectSources as NgrxEffectsSources } from '@ngrx/effects';
import { Observable } from 'rxjs';
import { Reducer, metaChannelReducer as createMetaChannelReducer, MetaReducer } from './reducers';
import { Action, Channel, createChannelAction } from './actions';
import { createMetaChannelEffects, NgrxEffectsInstance, EffectsInjectionToken, Effects } from './effects';
import { Selector } from './selectors';
import { filter, map } from 'rxjs/operators';
/**
 * @deprecated Will be removed in v7.0.0
 */
export const InitializeStoreActionType = '[Backbase Reducer] INITIALIZE REDUCER';
/**
 * @deprecated Will be removed in v7.0.0
 * An `Action` that is dispatched to a `Store` when it is created to allow the store to initialize
 * its initial state
 */
export class InitializeStoreAction implements Action {
  readonly type = InitializeStoreActionType;
}

/**
 * @deprecated Will be removed in v7.0.0
 * Stream operator to filter the `Actions` stream to only pass the `InitializeStoreAction`.
 * This can be used to write `Reducer`s or `Effect`s that are only run when their `Store` is
 * initially created.
 */
export const ofStoreInit = () => (source: Observable<Action>): Observable<InitializeStoreAction> =>
  source.pipe(filter((action): action is InitializeStoreAction => action.type === InitializeStoreActionType));
/**
 * @deprecated Will be removed in v7.0.0
 */
export type BranchName = string;
/**
 * @deprecated Will be removed in v7.0.0
 * The name of the store used to segregate the Store states within the application state branch.
 */
export type StoreName = string;

/**
 * @deprecated Will be removed in v7.0.0
 * A Store that maintains internal state which can be queried and acted upon.
 */
export interface Store<State, V extends Action> {
  /**
   * Dispatch an action to the store.
   * This may result in changes in state and/or external effects.
   */
  dispatch: (action: V) => void;

  /**
   * Select a projection of the internal state.
   */
  select: <R>(selector: Selector<State, R>) => Observable<R>;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export interface ReducerManager {
  addFeature<T, V extends Action>(featureDef: {
    key: string;
    reducers: Reducer<T, V>;
    reducerFactory: () => never;
    metaReducers: Array<MetaReducer<T, V>>;
  }): void;
}

export interface EffectSources<V extends Action> {
  addEffects(effects: NgrxEffectsInstance<V>): void;
}

/**
 * @internal
 */
@Injectable({ providedIn: 'root' })
export class StoreBroker {
  private readonly initializedStores = new Map<BranchName, Array<StoreName>>();

  constructor(
    @Inject(NgrxStore) private readonly superstore: Store<any, Action>,
    @Inject(NgrxActions) private readonly actions: Observable<Action>,
    @Inject(NgrxReducerManager) private readonly reducerManager: ReducerManager,
    @Inject(NgrxEffectsSources) private readonly effectsSources: EffectSources<any>,
    private readonly injector: Injector,
  ) {}

  initStore<T, V extends Action>(
    storeName: StoreName,
    reducer: Reducer<T, V>,
    effects: Array<EffectsInjectionToken<V>>,
    branchName: BranchName,
    dispatchChannel: Channel,
    inputChannels: Array<Channel> = [],
    widgetInjector?: Injector,
  ): Store<T, V> {
    if (this.isStoreInitialized(branchName, storeName)) {
      return this.createStore(storeName, branchName, dispatchChannel);
    }

    const allInputChannels = [this.privateChannel(storeName, dispatchChannel), ...inputChannels];

    this.addReducer(storeName, branchName, reducer, allInputChannels);

    this.setStoreInitialized(branchName, storeName);

    const store = this.createStore<T, V>(storeName, branchName, dispatchChannel);
    effects
      .map(token => (widgetInjector || this.injector).get<Effects<V>>(token))
      .forEach((effectInstance: Effects<V>) => {
        this.addEffects(store, effectInstance, this.privateChannel(storeName, dispatchChannel), allInputChannels);
      });

    store.dispatch(new InitializeStoreAction() as V);

    return store;
  }

  private createStore<T, V extends Action>(
    storeName: StoreName,
    branchName: BranchName,
    dispatchChannel: Channel,
  ): Store<T, V> {
    return {
      select: <R>(selector: (state: T) => R): Observable<R> => {
        return this.superstore.select<T>(state => state[this.privateBranch(storeName, branchName)]).pipe(map(selector));
      },
      dispatch: (action: V) => {
        this.dispatchForStore(storeName, dispatchChannel, action);
      },
    };
  }

  /**
   * Dispatches an action scoped on a channel just for this store.
   */
  private dispatchForStore(storeName: StoreName, dispatchChannel: Channel, action: Action) {
    this.dispatchOnChannel(this.privateChannel(storeName, dispatchChannel), action);
  }

  private dispatchOnChannel(channel: Channel, action: Action) {
    this.superstore.dispatch(createChannelAction(action, channel));
  }

  private isStoreInitialized(branchName: BranchName, storeName: StoreName): boolean {
    const storesInBranch = this.initializedStores.get(branchName);
    return !!(storesInBranch && storesInBranch.find(name => name === storeName));
  }

  private setStoreInitialized(branchName: BranchName, storeName: StoreName) {
    let storesInBranch = this.initializedStores.get(branchName);
    if (storesInBranch === undefined) {
      storesInBranch = [];
      this.initializedStores.set(branchName, storesInBranch);
    }
    storesInBranch.push(storeName);
  }

  private addReducer<T, V extends Action>(
    storeName: StoreName,
    branchName: BranchName,
    reducer: Reducer<T, V>,
    inputChannels: Array<Channel>,
  ) {
    this.reducerManager.addFeature({
      key: this.privateBranch(storeName, branchName),
      reducers: createMetaChannelReducer<T, V>(inputChannels)(reducer),
      reducerFactory: () => {
        throw new Error('No reducer factory');
      },
      metaReducers: [], //[createMetaChannelReducer(inputChannels)],
    });
  }

  private addEffects<V extends Action>(
    store: Store<any, V>,
    effects: Effects<V>,
    dispatchChannel: Channel,
    listenChannels: Array<Channel>,
  ): void {
    this.effectsSources.addEffects(
      createMetaChannelEffects(store, this.actions, effects, dispatchChannel, listenChannels),
    );
  }

  private privateBranch(storeName: StoreName, branchName: BranchName) {
    return `${storeName}/${branchName}`;
  }

  private privateChannel(storeName: StoreName, dispatchChannel: Channel) {
    return `${storeName}/${dispatchChannel}`;
  }
}
