import { ActionReducer } from '@ngrx/store';

import { Action, ActionCreator, isActionForChannel, ChannelAction, toChannelAction } from './actions';
/**
 * @deprecated Will be removed in v7.0.0
 */
export const InitializeStateActionType = '[Backbase Reducer] INITIALIZE REDUCER';
/**
 * @deprecated Will be removed in v7.0.0
 */
export class InitializeState implements Action {
  readonly type = InitializeStateActionType;
}

/**
 * @deprecated Will be removed in v7.0.0
 * Generate a new state (`T`) based on a previous state in response to an action (`V`)
 */
export type Reducer<T, V> = (state: T | undefined, action: V) => T;
/**
 * @deprecated Will be removed in v7.0.0
 */
export type MetaReducer<T, V extends Action = Action> = (reducer: Reducer<T, V>) => Reducer<T, V>;
/**
 * @deprecated Will be removed in v7.0.0
 */
export const metaChannelReducer = <T, V extends Action = Action>(
  channels: Array<string>,
): ((reducer: Reducer<T, V>) => Reducer<T, ChannelAction<V>>) => {
  // create a meta reducer (takes a reducer, and returns a new reducer)
  return (reducer: Reducer<T, V>): Reducer<T, ChannelAction<V>> => {
    // create a function to initialize the state for this reducer
    const initializeState = () => reducer(undefined, new InitializeState() as V);
    // return the new reducer, which filters actions not for this "channel"
    return (state: T = initializeState(), action: V | ChannelAction<V>): T => {
      const channelAction = toChannelAction(action);
      if (isActionForChannel(action, channels)) {
        return reducer(state, channelAction.action);
      }
      return state;
    };
  };
};

export { createReducer, on } from '@ngrx/store';

export { ActionReducer };
/**
 * @deprecated Will be removed in v7.0.0
 */
export type OnReducer<S, C extends Array<ActionCreator>> = (state: S, action: ActionType<C[number]>) => S;
/**
 * @deprecated Will be removed in v7.0.0
 */
export interface On<S> {
  reducer: ActionReducer<S>;
  types: Array<string>;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export type ActionType<A> = A extends ActionCreator<infer T, infer C>
  ? ReturnType<C> & {
      type: T;
    }
  : never;
