import { Action, Channel } from './actions';
import { Inject, InjectionToken, Injector } from '@angular/core';
import { Store, StoreBroker, BranchName, StoreName } from './store-broker.service';
import { Reducer } from './reducers';
import { EffectsInjectionToken } from './effects';
import { AvailableFeature, FeaturesService } from '@backbase/foundation-ang/future';
/**
 * @deprecated Will be removed in v7.0.0
 */
export const ENFORCE_INJECTOR_FOR_CREATE_STORE: AvailableFeature = {
  name: 'ENFORCE_INJECTOR_FOR_CREATE_STORE',
  description: 'you have to provide widget injector for createStore to make sure your widget support lazy loading',
};

/**
 * @deprecated Will be removed in v7.0.0
 * Configuration token that defines the "branch" of the application state that the store is responsible for.
 *
 * @internal
 */
export const STORE_BRANCH = new InjectionToken<BranchName>('@backbase store branch');

/**
 * @deprecated Will be removed in v7.0.0
 * Configuration token that defines the "channel" on which the store will dispatch any events it produces.
 *
 * @internal
 */
export const STORE_DISPATCH_CHANNEL = new InjectionToken<Channel>('@backbase store dispatch channel');

export { Store };

/**
 * @deprecated Will be removed in v7.0.0
 * Create a scoped store on a particular branch/channel
 */
export class StoreFactory {
  constructor(
    private readonly storeBroker: StoreBroker,
    @Inject(STORE_BRANCH) private readonly branch: BranchName,
    @Inject(STORE_DISPATCH_CHANNEL) private readonly dispatchChannel: Channel,
    private featuresService: FeaturesService,
  ) {}

  /**
   * @deprecated you have to provide widget injector for createStore to make sure your widget support lazy loading.
   * Create a `Store` that maintains state (`T`) and responds to `Action`s (`V`).
   * @param storeName The name of the store - this should be unique within the `branch`.
   * @param reducer The reducers that update the `Store`s state based on `Action`s dispatched to the `Store`.
   * @param effects The `Effect`s that trigger external effects and/or produce new `Action`s based on the `Action`s dispatched to the `Store`.
   **/
  createStore<T, V extends Action = Action>(
    storeName: StoreName,
    reducer: Reducer<T, V>,
    effects: Array<EffectsInjectionToken<V>>,
  ): Store<T, V>;
  /**
   * Create a `Store` that maintains state (`T`) and responds to `Action`s (`V`).
   * @param storeName The name of the store - this should be unique within the `branch`.
   * @param reducer The reducers that update the `Store`s state based on `Action`s dispatched to the `Store`.
   * @param effects The `Effect`s that trigger external effects and/or produce new `Action`s based on the `Action`s dispatched to the `Store`.
   * @param injector widget injector is required to support lazy loading.
   **/
  createStore<T, V extends Action = Action>(
    storeName: StoreName,
    reducer: Reducer<T, V>,
    effects: Array<EffectsInjectionToken<V>>,
    // eslint-disable-next-line @typescript-eslint/unified-signatures
    injector: Injector,
  ): Store<T, V>;
  createStore<T, V extends Action = Action>(
    storeName: StoreName,
    reducer: Reducer<T, V>,
    effects: Array<EffectsInjectionToken<V>>,
    injector?: Injector,
  ): Store<T, V> {
    if (!injector && this.featuresService.isEnabled(ENFORCE_INJECTOR_FOR_CREATE_STORE)) {
      throw new Error(ENFORCE_INJECTOR_FOR_CREATE_STORE.description);
    }

    if (!injector) {
      console.warn(ENFORCE_INJECTOR_FOR_CREATE_STORE.description);
    }

    return this.storeBroker.initStore(
      storeName,
      reducer,
      effects,
      this.branch,
      this.dispatchChannel,
      undefined,
      injector,
    );
  }
}
