import { Action, ChannelAction, Channel, isActionForChannel, createChannelAction, toChannelAction } from './actions';

import { createEffect } from '@ngrx/effects';
import { Observable } from 'rxjs';
import { map, filter } from 'rxjs/operators';
import { Type, InjectionToken } from '@angular/core';
import { Store } from './store-broker.service';

export const METADATA_KEY = '@backbase/store effect metadata';

/**
 * @deprecated Will be removed in v7.0.0
 * An instance of a class containing one or more `Effect` methods.
 */
// @todo: define effects class
// @ts-expect-error: The generic here is undefined, but left for future use.
export type Effects<V extends Action> = any;

/**
 * @deprecated Will be removed in v7.0.0
 * An injection token used to get an `Effect`s instance.
 */
export type EffectsInjectionToken<V extends Action> = Type<Effects<V>> | InjectionToken<Effects<V>>;
/**
 * @deprecated Will be removed in v7.0.0
 */
export interface NgrxEffectsInstance<V extends Action> {
  [property: string]: Observable<V>;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
// @ts-expect-error: The generic here is undefined, but left for future use.
export interface EffectMetadata<T> {
  // Once TS is >= 2.8 replace with <Key extends Extract<keyof T, string>>
  propertyName: string;
  // @todo: dispatch optional (same as ngrx)
}

const setEffectActionMetadata = <T extends object>(sourceProto: T, entries: Array<EffectMetadata<T>>) => {
  const constructor = sourceProto.constructor;
  const meta: Array<EffectMetadata<T>> = constructor.hasOwnProperty(METADATA_KEY)
    ? (constructor as any)[METADATA_KEY]
    : (Object.defineProperty(constructor, METADATA_KEY, { value: [] }) as any)[METADATA_KEY];
  Array.prototype.push.apply(meta, entries);
};

const getEffectsMetadata = <T extends object>(sourceProto: T): Array<EffectMetadata<T>> =>
  sourceProto.constructor.hasOwnProperty(METADATA_KEY) ? (sourceProto.constructor as any)[METADATA_KEY] : [];

/**
 * @deprecated Will be removed in v7.0.0
 * Method decorator to mark a method as an effect. Effects can be registered with a `Store` to perform external
 * effects such as fetching data or navigating the browser.
 *
 * The decorated method must be `(actions: Observable<Action>, store?: Store) => Observable<Action>`.
 */
export const Effect = <T extends object>(): MethodDecorator => {
  return function (target: T, methodName: string) {
    const metadata = {
      propertyName: methodName,
    };
    setEffectActionMetadata(target, [metadata]);
  } as (target: {}, propertyName: string | symbol) => void;
};

const hasEffectActionMetadata = (instance: any): boolean => getEffectsMetadata(instance).length > 0;

const outputChannel = <T extends Action>(channel: string) => (source: Observable<T>): Observable<ChannelAction<T>> =>
  source.pipe(map((action: T) => createChannelAction(action, channel)));

const ofChannel = <V extends Action>(channels: Array<string>) => (source: Observable<Action>): Observable<V> =>
  source.pipe(
    map(action => toChannelAction(action)),
    filter<ChannelAction<Action>, ChannelAction<V>>((action): action is ChannelAction<V> =>
      isActionForChannel(action, channels),
    ),
    map(channelAction => channelAction.action),
  );
/**
 * @deprecated Will be removed in v7.0.0
 */
export const createMetaChannelEffects = <V extends Action>(
  store: Store<unknown, V>,
  actions: Observable<Action>,
  effects: Effects<Action>,
  dispatchChannel: Channel,
  listenChannels: Array<Channel>,
): NgrxEffectsInstance<ChannelAction<Action>> => {
  if (!hasEffectActionMetadata(effects)) {
    throw new Error('Effect class has no effects');
  }
  const proto = function () {};
  Object.defineProperty(proto, 'name', { value: `Effect${dispatchChannel}`, writable: false });
  const effectInstance: NgrxEffectsInstance<ChannelAction<Action>> = Object.create(proto);

  return getEffectsMetadata(effects).reduce(
    (instance: NgrxEffectsInstance<ChannelAction<Action>>, effect: EffectMetadata<any>) => {
      instance[effect.propertyName] = createEffect(() =>
        effects[effect.propertyName](actions.pipe(ofChannel(listenChannels)), store).pipe(
          outputChannel(dispatchChannel),
        ),
      );
      return instance;
    },
    effectInstance,
  );
};
