import { ActionCreator } from '@ngrx/store';
import { Observable } from 'rxjs';
import { filter } from 'rxjs/operators';

/**
 * @deprecated Will be removed in v7.0.0
 * A `Channel` provides a way to limit the scope of `Action`s. `Effect`s and `Reducer`s will only
 * respond to `Action`s that match their `Channel`.
 */
export type Channel = string;

/**
 * @deprecated Will be removed in v7.0.0
 * An `Action` describes some request or event that a store may wish to respond to, either by
 * updating its internal state or triggering external effects and dispatching follow-up `Action`s
 */
export interface Action {
  /**
   * An identifier to uniquely identify the `Action` in the context of a `Store`.
   */
  type: string;
}

/**
 * @deprecated Will be removed in v7.0.0
 * A stream of `Action`s that a `Reducer` or `Effect` may respond to.
 */
export type Actions<V extends Action = Action> = Observable<V>;
/**
 * @deprecated Will be removed in v7.0.0
 */
export interface ChannelAction<V extends Action = Action> extends Action {
  type: string;
  channel: Channel | undefined;
  action: V;
}

/**
 * @deprecated Will be removed in v7.0.0
 */
export const createChannelAction = <V extends Action>(action: V, channel?: Channel): ChannelAction<V> => ({
  type: action.type,
  channel,
  action,
});

const isChannelAction = <V extends Action>(action: V | ChannelAction<V>): action is ChannelAction<V> =>
  'channel' in action && 'action' in action;

/**
 * @deprecated Will be removed in v7.0.0
 */
export const toChannelAction = <V extends Action>(action: V | ChannelAction<V>): ChannelAction<V> =>
  isChannelAction(action) ? action : createChannelAction(action);

/**
 * @deprecated Will be removed in v7.0.0
 */
export const isActionForChannel = <V extends Action>(action: V, channels: Array<Channel>): boolean => {
  return !isChannelAction(action) || action.channel === undefined || channels.includes(action.channel);
};

/**
 * @deprecated Will be removed in v7.0.0
 * Stream operator to filter the `Actions` stream to only pass `Action`s that match the given `type`.
 */
export const ofType = <R extends V, V extends Action = Action>(
  type: string | ActionCreator<string, FunctionWithParametersType<Array<any>, R>>,
) =>
  typeof type === 'string'
    ? (actions: Actions<V>): Actions<R> => actions.pipe(filter((action: V): action is R => action.type === type))
    : (actions: Actions<V>): Actions<ReturnType<typeof type>> =>
        actions.pipe(filter((action: V): action is R => action.type === type.type));

/**
 * @deprecated Will be removed in v7.0.0
 */
export { createAction, ActionCreator, props } from '@ngrx/store';

/**
 * @deprecated Will be removed in v7.0.0
 */
export type Creator<P extends Array<any> = Array<any>, R extends object = object> = R extends { type: any }
  ? TypePropertyIsNotAllowed
  : FunctionWithParametersType<P, R>;
/**
 * @deprecated Will be removed in v7.0.0
 */
export const typePropertyIsNotAllowedMsg = 'type property is not allowed in action creators';
/**
 * @deprecated Will be removed in v7.0.0
 */
export type TypePropertyIsNotAllowed = typeof typePropertyIsNotAllowedMsg;

/**
 * @deprecated Will be removed in v7.0.0
 */
export type FunctionWithParametersType<P extends Array<unknown>, R = void> = (...args: P) => R;
/**
 * @deprecated Will be removed in v7.0.0
 */
export interface TypedAction<T extends string> extends Action {
  readonly type: T;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export type DisallowTypeProperty<T> = T extends { type: any } ? TypePropertyIsNotAllowed : T;
/**
 * @deprecated Will be removed in v7.0.0
 */
export type PropsReturnType<T extends object> = T extends { type: any }
  ? TypePropertyIsNotAllowed
  : { _as: 'props'; _p: T };
